.SearchBar {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 6rem;
  margin-bottom: 4rem;
}

.SearchBar input {
  width: 287px;
  padding: 0.88rem 0;
  border: 1px solid #fff;
  border-radius: 3px;
  margin-bottom: 2.22rem;
  color: #010c3f;
  text-align: center;
  font-size: 1rem;
  background-color: rgba(256, 256, 256, 0.8);
}

.SearchBar input:focus {
  outline: none;
}

.SearchBar button {
  cursor: pointer;
  width: 8.11rem;
  padding: 0.77rem 0;
  border-radius: 54px;
  background-color: #006477;
  text-align: center;
  font-size: 0.833rem;
  transition: background-color 0.25s;
  color: white;
}

.SearchBar button:hover {
  background-color: #00404F;
}