.Track {
  display: flex;
  align-items: center;
  border-bottom: 1px solid rgba(256, 256, 256, 0.8);
}

.Track-action {
  cursor: pointer;
  padding: 0.5rem;
  font-size: 1.6rem;
  transition: color 0.25s;
  border: none;
  color: white;
  background-color: rgba(0, 0, 0, 0);
}

.Track-action:hover {
  color: rgba(265, 265, 265, 0.5);
}

.Track-information {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 72px;
  overflow: hidden;
}

.Track-information h3 {
  margin-bottom: 0.22rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.Track-information p {
  font-size: 0.83rem;
  font-weight: 300;
  color: rgba(256, 256, 256, 0.8);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 0;
  padding: 0;
}

.track-img {
  max-width: 70px;
  height: auto;
  margin-right: 10px;
}

.render-action-container {
  display: flex;
  align-items: center;
}

.play-pause {
  cursor: pointer;
  padding: 0.5rem;
  font-size: 1.6rem;
  border: none;
  color: white;
  background-color: rgba(0, 0, 0, 0);
}